'use strict';

var React = require('react');
var Reflux = require('reflux');

module.exports=React.createClass({displayName: "exports",

	componentWillReceiveProps:function(){
		console.log('layers->manager->esriLayer: componentWillReceiveProps');
	},

	update:function(){
		this.props.layer.defaultVisibility=!this.props.layer.defaultVisibility;
	    this.props.onChange();
		this.forceUpdate();
	},

	

	render: function() {
		
		/*use this.props.layer */
		console.log("layers->manager->esriLayer: render");

		return (React.createElement("li", null, 
			
			React.createElement("input", {name: this.props.layer.id, checked: this.props.layer.defaultVisibility, onChange: this.update, type: "checkbox"}), 
		      	this.props.layer.name
		     ))
	}
});