/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var Reflux = require('reflux');
var FilterMap = require('../../../conf/filterMap.js');

var FilterGroup = require('./filterGroup.jsx');
var FilterGroupTree = require('./filterGroupTree.jsx');
var FilterGroupWithSubLevels = require('./filterGroupWithSubLevels.jsx');
var FilterActionButton = require('./filterActionButton.jsx');
var TabbedArea = require('react-bootstrap/lib/TabbedArea');
var TabPane = require('react-bootstrap/lib/TabPane');

var FilterActions = require('../../../actions/filterActions.js');
var FilterStore=require('../../../stores/filterStore.js');


var Filter  = React.createClass({displayName: "Filter",
    mixins: [Reflux.connect(FilterStore)],

    _onItemChanged: function(filterType, id, value) {     
        FilterActions.changeFilterItemState(filterType, id, value);
    },

    _onClickApply: function(event) {     
        FilterActions.triggerFilterApply();
    },

    _onClickReset: function(event) {     
        FilterActions.triggerFilterReset();
    },
    
    _onAllNoneClicked: function(filterType, selected) {
        FilterActions.changeAllFilterItemState(filterType, selected);  
    },    
    
    render: function() {
      var filters = FilterMap.filters;
      var self = this;
        return(
          React.createElement("div", {className: "activity-nav"}, 
            React.createElement(TabbedArea, {className: "activities", defaultActiveKey: 1}, 
              
                filters.map(function(filterDefinition){
                  if (!filterDefinition.subLevels){
                    var group = React.createElement(FilterGroup, {filterDefinition: filterDefinition, onItemChanged: self._onItemChanged, onAllNoneClicked: self._onAllNoneClicked})
                  } else {
                    if (filterDefinition.showTree){
                      var group = React.createElement(FilterGroupTree, {filterDefinition: filterDefinition, onItemChanged: self._onItemChanged, onAllNoneClicked: self._onAllNoneClicked})
                    } else {
                      var group = React.createElement(FilterGroupWithSubLevels, {filterDefinition: filterDefinition, onItemChanged: self._onItemChanged, onAllNoneClicked: self._onAllNoneClicked})
                    }                    
                  }
                  return React.createElement(TabPane, {eventKey: parseInt(filterDefinition.index), tab: filterDefinition.label}, 
                    group
                  )                  
                })
              
            ), 
            React.createElement(FilterActionButton, {onClickReset: this._onClickReset, onClickApply: this._onClickApply})
          )
        );
        
    }
});

module.exports = Filter;