
var React = require('react');

var SelectionCounter = React.createClass({displayName: "SelectionCounter",

    _showSelected: function(event) {     
        this.props.onCounterClicked(true);
    },

    _showAll: function(event) {     
        this.props.onCounterClicked(false);
    },

    componentDidMount: function(){
        $('.filter-count').tooltip();      
    },

    render: function() {
        var divStyle = {display: 'inline'};
        return(
              React.createElement("div", {style: divStyle}, 
                  React.createElement("span", {className: "filter-count", title: "Show All", "data-placement": "left"}, 
                    React.createElement("a", {href: "#", onClick: this._showAll}, this.props.total)
                  ), 
                  React.createElement("span", {className: "filter-count"}, "/"), 
                  React.createElement("span", {className: "filter-count", title: "Show Only Selected", "data-placement": "left"}, 
                    React.createElement("a", {href: "#", onClick: this._showSelected}, this.props.selected)
                  )
              )
            );
    }
});

module.exports = SelectionCounter;