'use strict';

var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;

var LayersAction=require('../../../../actions/layersAction.js');
var DataLayerStore=require('../../../../stores/dataLayerStore.js');
var RadioButton=require('../../../commons/customRadioButton.jsx');




module.exports  = React.createClass({displayName: "exports",
 mixins: [Reflux.connect(DataLayerStore, 'layers')],

	showByDepartment:function(){
		console.log('layers->dataLayerSelector: ShowByDepartment');
		LayersAction.loadActivitiesByDepartments();
	},

	showByMunicipality:function(){
		console.log('layers->dataLayerSelector: showByMunicipality');
		LayersAction.loadActivitiesByMuncipalities();
	},

	enableFinancingSelector:function(event){
		this.setState({'layerLevel': 'Financing'});
	},

	enableIndicatorSelector:function(event){
		this.setState({'layerLevel': 'Indicators'});
	},

	render: function() {
		var finSelectorClass = this.state.layerLevel == 'Financing'? "" : "disabled";
		var indSelectorClass = this.state.layerLevel == 'Indicators'? "" : "disabled";
		return (  
		React.createElement("div", {className: "inline"}, 
			React.createElement("div", {className: ""}, 
				React.createElement(RadioButton, {name: "layerLevel", label: "Financing", onClick: this.enableFinancingSelector}), 
				React.createElement("div", {className: finSelectorClass}, 
					React.createElement("ul", null, 
						React.createElement("li", null, 
							React.createElement(RadioButton, {name: "finLevel", onClick: this.showByDepartment, label: "layers.byDepartment"}), 
							React.createElement(RadioButton, {name: "finLevel", onClick: this.showByMunicipality, label: "layers.byMunicipality"})	
						)
					)					
				)
			), 
			React.createElement("div", {className: ""}, 
				React.createElement(RadioButton, {name: "layerLevel", label: "Indicators", onClick: this.enableIndicatorSelector}), 
				React.createElement("div", {className: indSelectorClass}, 
					React.createElement("ul", null, 
						React.createElement("li", null, 
							React.createElement(RadioButton, {name: "indLevel", onClick: this.showByDepartment, label: "layers.byDepartment"}), 
							React.createElement(RadioButton, {name: "indLevel", onClick: this.showByMunicipality, label: "layers.byMunicipality"})
						)
					)
				)
			)	
		)   
		);
	}

});